@tailwind base;
@tailwind components;
@tailwind utilities;

p {
  color: rgba(4, 17, 55, 0.7);
  line-height: 175%;
}

@layer components {
  .common-btn {
    @apply px-5 py-3 no-underline rounded border border-solid border-primary bg-primary/[0.06] hover:bg-primary/[0.12] text-base text-primary hover:text-primary leading-[normal] transition h-max focus:outline-unset focus:box-shadow-unset focus:text-primary;
  }

  .common-btn-two {
    @apply block text-center px-4 md:px-5 py-2 cursor-pointer md:py-3 bg-primary/[0.06] no-underline border border-solid border-primary rounded text-primary capitalize relative z-0 transition duration-500 overflow-hidden before:absolute before:top-0 before:left-0 before:h-full before:bg-primary before:-z-10 before:w-2/4 before:-translate-x-full hover:before:translate-x-0 before:transition before:duration-500 after:absolute  after:top-0 after:right-0 after:h-full after:bg-primary after:-z-10 after:w-2/4 after:translate-x-full hover:after:translate-x-0 after:transition after:duration-500 hover:text-white;
  }

  .drag-none {
    -webkit-user-drag: none;
    -khtml-user-drag: none;
    -moz-user-drag: none;
    -o-user-drag: none;
    -ms-user-drag: none;
    user-drag: none;
  }

  img {
    @apply select-none drag-none;
  }

  .outline-unset {
    outline: unset;
  }
  .box-shadow-unset {
    box-shadow: unset;
  }
  .unlockafe_addon {
    margin-top: 20px;
  }
  .primary-gradient {
    background: linear-gradient(
      129deg,
      #5420c5 -64.95%,
      #8436cb 12.88%,
      #de5fd5 147.61%
    );
  }

  .preloader {
    position: relative;
    z-index: 1222;
    inset: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 130px;
  }
  .preloader .loading-overlay {
    position: absolute;
    background-color: rgba(255, 255, 255, 0.1098039216);
    width: 100%;
    height: 100%;
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
  }
  .preloader .custom-loader {
    display: inline-block;
    position: relative;
    width: 74px;
    height: 100px;
    align-items: center;
    justify-content: center;
  }
  .preloader .custom-loader div {
    position: absolute;
    top: 33px;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: #a04eec;
    animation-timing-function: cubic-bezier(0, 1, 1, 0);
  }
  .preloader .custom-loader div:nth-child(1) {
    left: 8px;
    animation: marketi-preloader1 0.6s infinite;
  }
  .preloader .custom-loader div:nth-child(2) {
    left: 8px;
    animation: marketi-preloader2 0.6s infinite;
  }
  .preloader .custom-loader div:nth-child(3) {
    left: 32px;
    animation: marketi-preloader2 0.6s infinite;
  }
  .preloader .custom-loader div:nth-child(4) {
    left: 56px;
    animation: marketi-preloader3 0.6s infinite;
  }

  @keyframes marketi-preloader1 {
    0% {
      transform: scale(0);
    }
    100% {
      transform: scale(1);
    }
  }
  @keyframes marketi-preloader3 {
    0% {
      transform: scale(1);
    }
    100% {
      transform: scale(0);
    }
  }
  @keyframes marketi-preloader2 {
    0% {
      transform: translate(0, 0);
    }
    100% {
      transform: translate(24px, 0);
    }
  }
}
